import React, { useContext } from "react";
import SectionContainer from "../../containers/SectionContainer";
import Gallery from "../../components/Gallery";
import { useTranslation } from "react-i18next";
import { makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import ThemeContext from '../../contexts/themeContext';
import { Canvas } from '@react-three/fiber';
import { Asset3D, AssetType, Camera3D } from "../../components/Asset3D";

const ImmersiveSection = () => {
    const { t } = useTranslation();
    const classes = useStyles();
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

    let settings = {};

    if(isMobile) {
        settings.transform = {
            position: [0, 0.3, 0],
            rotation: [0, Math.PI/2, 0],
            scale: 0.25,
            rotationFactor: Math.PI / 2,
        };
    } else {
        settings.transform = {
            position: [-0.6, 0.3, 0],
            rotation: [0, Math.PI/2, 0],
            scale: 0.5,
            rotationFactor: Math.PI / 2, // scrollRotationFactor = Math.PI * 2 for full scroll
        };
    }

    return (
        <>
        {/*
        <div className={classes.canvasContainer}>
            <Canvas camera={{ position: [0, 0, 5] }} >
                <Camera3D fov={10} zoom={1}/>

                <directionalLight position={[5, 5, 5]} intensity={isDarkMode ? 0.5 : 2} castShadow={false}/>
            
                <Asset3D type={AssetType.Immersive} position={settings.transform.position} rotation={settings.transform.rotation} scale={settings.transform.scale} scrollRotationFactor={settings.transform.rotationFactor} />
            </Canvas>
        </div>
        */}
        

        <SectionContainer id="immersive_section" title={t('menu_immersive_section')}>
        <Gallery type={"immersive"} />
        </SectionContainer>
        </>
        
    );
};

const useStyles = makeStyles((theme) => ({
    canvasContainer: {
        position: "relative",
        height: "200vh",
        width: "100%",
        pointerEvents: "auto",
        marginTop: "25vh",
        [theme.breakpoints.down("sm")]: {
            marginTop: "25vh",
        },
        marginBottom: "-75vh",
        //zIndex: -1,
        //backgroundColor: "red",
    },
}));

export default ImmersiveSection;
